import * as React from 'react';
// import { styled } from '@mui/material/styles';
import { Button, Switch, TextField } from '@mui/material';
import Chip from '@mui/material/Chip';
import DoneIcon from '@mui/icons-material/Done';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Stack from '@mui/material/Stack';
import LocalGroceryStoreIcon from '@mui/icons-material/LocalGroceryStore';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import SaveAltIcon from '@mui/icons-material/SaveAlt';
import { useHistory } from "react-router-dom";
import IconButton from "@mui/material/IconButton";
import ClearIcon from '@mui/icons-material/Clear';
import Axios from 'axios'
import { useToasts } from "react-toast-notifications";


export default function SwitchListSecondary() {
      const history = useHistory();
      const { addToast } = useToasts();
      const handleDelete = () => {};
      const gotoItems =()=>{
            history.push('/items')
      }
      const prevPage=()=>{
            history.push('/translist')
      }
      const signOff = async () =>{
            let newProfile ={
                  email,
                  showCant,
                  directSale,
                  quickPrint,
                  showMultiItems,
                  formatShareA4,
                  formatPrintA4,
                  itemsIgv,
                  defaultText,
            }
            const updateProfile = await Axios.put(
                  "/users/updateProfile",
                  newProfile
                );
            
                // Register Success Case
                if (updateProfile.status === 200) {
                  addToast("Saved successfully", {
                        appearance: "success",
                        autoDismiss: true,
                      });
                      history.push('/translist')
                } else {
                  addToast(updateProfile.data.msg, {
                    appearance: "error",
                    autoDismiss: true,
                  });
                }
      }
      const [showCant, setShowCant] = React.useState(false)
      const [directSale, setDirectSale] = React.useState(false)
      const [quickPrint, setQuickPrint] = React.useState(false)
      const [showMultiItems, setShowMultiItems] = React.useState(false)
      const [formatShareA4, setFormatShareA4] = React.useState(false)
      const [formatPrintA4, setFormatPrintA4] = React.useState(false)
      const [itemsIgv, setItemsIgv] = React.useState(false)
      const [email, setEmail] = React.useState('')
      const [defaultText, setDefaultText] =React.useState('');
      
      const [docType, setDocType] = React.useState({
            invoice: false,
            ticket: false,
            note: false
          });
      React.useEffect(() => {
            const checkLoggedIn = async () => {
                let token = localStorage.getItem("auth-token");
                if (!token) {
                  history.push("/");
                  return;
                }
                  const userRes = await Axios.get("/users/", {
                    headers: { "x-auth-token": token },
                  });
                  
                  setEmail(userRes.data.email);
                Axios.get("/users/getId/" + userRes.data.email).then(async (response) => {
                  setShowCant(response.data.showCant);
                  setDirectSale(response.data.directSale);
                  setQuickPrint(response.data.quickPrint);
                  setShowMultiItems(response.data.showMultiItems);
                  setFormatShareA4(response.data.formatShareA4);
                  setFormatPrintA4(response.data.formatPrintA4);
                  setItemsIgv(response.data.itemsIgv);
                  setDefaultText(response.data.defaultText);
                  setDocType({...docType, 'invoice' : response.data.docType[0], 'ticket' : response.data.docType[1], 'note' : response.data.docType[2]})
                });
              
          };
        checkLoggedIn();
    
        return()=>{
        } // eslint-disable-next-line react-hooks/exhaustive-deps
        },[]);
    
     
  return (
      <div> 
            <p style={{width: '100%',
                        textAlign:'center',
                        fontWeight: 900,
                        fontSize:'30px',
                        marginTop : '20px',
                        color: '#1976d2',
                  }}>Profile</p>
                  <IconButton color="primary" aria-label="back" onClick={prevPage} sx={{position : 'absolute', top: '10px', right: '10px', zIndex:1}}>
    <ClearIcon />
  </IconButton>
       <List
      sx={{ px:2, pt:1, bgcolor: 'background.paper' , height:'100vh'}}
    >
      <Button onClick={gotoItems} endIcon={<LocalGroceryStoreIcon />} variant="outlined" sx={{ marginBottom: '10px', ontWeight: 600}} fullWidth>Items Show</Button>
      <ListItem  sx={{borderBottom: "2px solid #1976d2"}}>
            <ListItemText primary="Show Count" />
                  <Switch edge="end" checked={showCant} onChange={(event)=>{
                        setShowCant(event.target.checked);
                  }} />
      </ListItem>
      <ListItem  sx={{borderBottom: "2px solid #1976d2"}}>
            <ListItemText primary="Direct Sale" />
            <Switch edge="end" checked={directSale} onChange={(event)=>{
                        setDirectSale(event.target.checked);
                  }} />
      </ListItem>
      <ListItem  sx={{borderBottom: "2px solid #1976d2"}}>
            <ListItemText primary="Quick Print"  />
            <Switch edge="end" checked={quickPrint} onChange={(event)=>{
                        setQuickPrint(event.target.checked);
                  }}/>
      </ListItem>
      <TextField
                  sx={{my:1}}
                  label="Default Description"
                  size="small"
                  variant="standard"
                  fullWidth
                  focused
                  value= {defaultText}
                  onChange={(e) => setDefaultText(e.target.value)}
                  placeholder='You can set default description'
                  />
      <ListItem  sx={{borderBottom: "2px solid #1976d2"}}>
            <ListItemText primary="Show Multi Items" />
            <Switch edge="end" checked={showMultiItems} onChange={(event)=>{
                        setShowMultiItems(event.target.checked);
                  }}/>
      </ListItem>
      <FormControl sx={{mt:1 , borderBottom: "2px solid #1976d2", pb:1, width:'100%'}}>
         <FormLabel id="demo-controlled-radio-buttons-group" sx={{fontWeight:600}}>Documents allowed</FormLabel>
         <Stack direction="row" spacing={1} sx={{mx:'auto'}}>
         <Chip
            sx={{mx:1}}
            label="Invoice"
            onDelete={handleDelete} color="info"
            deleteIcon={<DoneIcon />}
            disabled={!docType.invoice}
            />
            <Chip
            sx={{mx:1}}
            label="Ticket"
            onDelete={handleDelete} 
            color="info"
            deleteIcon={<DoneIcon />}
            disabled={!docType.ticket}
            />
            <Chip
                  sx={{mx:1}}
            label="Note"
            onDelete={handleDelete} color="info"
            deleteIcon={<DoneIcon />}
            disabled={!docType.note}
            />

    </Stack>
            </FormControl>
     
      <ListItem  sx={{borderBottom: "2px solid #1976d2"}}>
            <ListItemText  primary="Format Share A4" />
            <Switch edge="end" checked={formatShareA4} onChange={(event)=>{
                        setFormatShareA4(event.target.checked);
                  }}/>
      </ListItem>
      <ListItem  sx={{borderBottom: "2px solid #1976d2"}}>
            <ListItemText  primary="Format Print A4" />
            <Switch edge="end" checked={formatPrintA4} onChange={(event)=>{
                        setFormatPrintA4(event.target.checked);
                  }}/>
      </ListItem>
      <ListItem  sx={{borderBottom: "2px solid #1976d2"}}>
            <ListItemText  primary="ITEMS IGV?" />
            <Switch edge="end" checked={itemsIgv}  onChange={(event)=>{
                        setItemsIgv(event.target.checked);
                  }}/>
      </ListItem>
      <Button endIcon={<SaveAltIcon />} variant="contained" sx={{ 
                        marginTop: '10px',  fontWeight: 600}} onClick={signOff} >Sign Off</Button>
    </List>
    
    </div>
   
  );
}
