import React, { useState, useEffect } from "react";
import Box from "@mui/material/Box";
// import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import Axios from "axios";
import InputBase from "@mui/material/InputBase";
import Button from "@mui/material/Button";
import { useToasts } from "react-toast-notifications";
// import { useDispatch } from "react-redux";
//
import AddIcon from '@mui/icons-material/Add';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import Paper from "@mui/material/Paper";
import IconButton from "@mui/material/IconButton";
import DeleteIcon from '@mui/icons-material/Delete'
import ClearIcon from '@mui/icons-material/Clear';
import EditIcon from '@mui/icons-material/Edit';
// import * as Actions from "../../redux/action";

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';

import TextField from '@mui/material/TextField';

import {
    SwipeableList,
    SwipeableListItem,
    SwipeAction,
    TrailingActions,
    Type as ListType
  } from "react-swipeable-list";
  import "react-swipeable-list/dist/styles.css";
 

export default function Products() {
    // const dispatch = useDispatch();
    const history = useHistory();
    const { addToast } = useToasts();
    const [allItems, setAllItems] = useState([]);
    const [item, setItem] = useState({
        email: '',
        item : ''
    });

    useEffect(() => {
        const checkLoggedIn = async () => {
            localStorage.removeItem('customerId')
            let token = localStorage.getItem("auth-token");
            if (!token) {
              history.push("/");
              return;
            }
              const userRes = await Axios.get("/users/", {
                headers: { "x-auth-token": token },
              });
              
              setItem({...item, email : userRes.data.email});
            Axios.get("/users/getId/" + userRes.data.email).then(async (response) => {
               setAllItems(response.data.items);
            });

      };
    checkLoggedIn();

    return()=>{
        setItem();
        setAllItems([]);
    } // eslint-disable-next-line react-hooks/exhaustive-deps
    },[]);


    // Go to previous page.
    const prevPage = () => {
        history.push('/profile');
    };
    const addItem = () => {
        if(allItems.length === 20){
            addToast("Limitation 20", {
                appearance: "warning",
                autoDismiss: true,
            });
            return;
        }
        if(item.item === ''){
            addToast("Entry the name of item", {
                appearance: "warning",
                autoDismiss: true,
            });
            return;
        }
        Axios.post('users/items/add', { email : item.email, item: [item.item]}).then( async (response)=>{
            setAllItems(response.data.items);
        })
    }
    const deleteItem = async (row) => {
       await Axios.post('users/items/delete', {email: item.email, item: row})
    }
    const editItem = ()=>{
        Axios.put('users/items/update', {email: item.email, index: changeData.index, item: changeData.temp}).then( async (response)=>{
            setAllItems(response.data.items);
        })
        setOpen(false);
    }

  const [open, setOpen] = React.useState(false);
  const [changeData, setChangeData] = useState({
    temp: '',
    index: null
  });
  const handleEdit = (row, index) => () => {
    setChangeData({
        temp: row,
        index: index
    })
    setOpen(true);
  }

  const handleClose = () => {
    setOpen(false);
  };

   const trailingActions = (row, index) => (
    <TrailingActions sx={{overflow:'hidden'}}>
      <SwipeAction  sx={{width:'10px'}} onClick={handleEdit(row, index)}>
          
            <Button variant="text" endIcon={<EditIcon  size="large"/>}>
            </Button>
            
      </SwipeAction>
      <SwipeAction destructive={true} onClick={()=>deleteItem(row)}>
       
      <Button variant="text"  startIcon={<DeleteIcon size="large" />}>
            </Button>
      </SwipeAction>
    </TrailingActions>
  );


    return (
            <Box sx={{ height:'100vh', backgroundColor:'#EDEDF5'}}>
                <IconButton color="primary" aria-label="back" onClick={prevPage} sx={{position : 'absolute', top: '10px', right: '10px'}}>
                    <ClearIcon />
                </IconButton>
                <p style={{ letterSpacing: '3px', fontWeight:'500',paddingBottom:"20px", fontSize: '20px', color : "#1976d2", paddingTop:'40px'}}>ITEMS</p>

                    <Paper
                        sx={{
                            px:1,
                            mx:1,
                            mb:0,
                            display: "flex",
                            alignItems: "center",
                            marginBottom: "13px",
                            boxShadow: "unset",
                            border: "1px solid #f5f5f5",
                        }}
                    >
                        <InputBase
                            sx={{ ml: 1, flex: 1 }}
                            placeholder="Add Items"
                            value = {item.item}
                            onChange={(e)=>{
                                setItem({...item, item: e.target.value})
                            }}
                            inputProps={{ "aria-label": "search google maps" }}
                        />
                        <IconButton onClick={ ()=> addItem()} sx={{ p: "10px" }} aria-label="search">
                            <AddIcon  color="primary"/>
                        </IconButton>
                    </Paper>

                    <Paper sx={{ overflow: 'hidden',  width:'100%',backgroundColor:'#EDEDF5',mt:"0", boxShadow:'0px 0px 0px 0px' }}>
                        <TableContainer sx={{mx:'auto',width:'95%',Index:'0', maxHeight: 'calc(100vh - 225px)' }}>
                            <Table aria-label="sticky table">
                            <TableBody>
                            <SwipeableList sx={{p:0, m:0}} type={ListType.IOS}>
                            {allItems.map((row, index) => (
                                <SwipeableListItem
                                key={index}
                                //leadingActions={leadingActions()}
                                trailingActions={trailingActions(row, index)}
                              >
                                <TableCell 
                                 key={index}
                                 scope="row" sx={{p:1,pl:1,mb:1, width:'100%', borderLeft:'3px solid grey'}}>
                                    <strong>{row}</strong>
                                
                                </TableCell>
                                </SwipeableListItem>
                            )).reverse().slice(0, 20)
                            }</SwipeableList>
                            </TableBody>
                            </Table>
                        </TableContainer>
                    </Paper>
                    
                    
                    <Dialog fullWidth open={open} onClose={handleClose}>
                        <DialogTitle>Edit Items</DialogTitle>
                        <DialogContent>
                        <TextField
                            autoFocus
                            focused
                            margin="dense"
                            id="name"
                            label="Name"
                            type="text"
                            fullWidth
                            variant="standard"
                            value= {changeData.temp}
                            onChange = {e => setChangeData({...changeData, temp : e.target.value})}
                        />
                        </DialogContent>
                        <DialogActions>
                        <Button onClick={handleClose}>Cancel</Button>
                        <Button onClick={editItem}>Change</Button>
                        </DialogActions>
                    </Dialog>
            </Box>
    );
}
